.contrastTable {
    background-color: #f0f0f0;
    padding: 16px;
    border-radius: 4px;
    width: fit-content;
}

table, th, td {
    border: 4px solid white;
    border-radius: 4px;
    border-collapse: collapse;
  }

  th, td {
    padding: 10px;
    background-color: #f0f0f0;
    min-width: 125px; 
    font-size: 14px;
  }